.tile-wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 70%;
    background-color: white;
    padding: 15px;
    border-radius: 5px;
    color: rgb(238, 232, 232) !important;
    -webkit-transition: background-color 0.2s;
    -o-transition: background-color 0.2s;
    transition: background-color 0.2s;
    position: relative;
    -webkit-box-shadow: 0 0 20px rgb(163, 163, 163);
            box-shadow: 0 0 20px rgb(163, 163, 163);
}

.text-col {
    color: black;
}

.image-col {

}

h3 {
    font-family: 'Fira Sans';
    font-weight: 600;
    font-size: 30px;
}

@media (max-width: 991px) {
    .tile-wrapper {
        width: 100%;
    }
}

@media (max-width: 767px) {
    .tile-wrapper {
        display: block;
        margin-bottom: 40px;
    }
}

@media (max-width: 575px) {
    .tile-wrapper {
        width: 70%;
    }
}

